<template>
  <div>
    <span>数据统计</span>
    <el-row :gutter="20">
      <el-col :span="6">
        <div>
          <el-statistic
            group-separator=","
            :precision="0"
            :value="value2"
            :title="title"
          ></el-statistic>
        </div>
      </el-col>
      <el-col :span="6">
        <div>
          <el-statistic title="男女比">
            <template slot="formatter"> 3/2 </template>
          </el-statistic>
        </div>
      </el-col>
      <el-col :span="6">
        <div>
          <el-statistic
            group-separator=","
            :precision="0"
            decimal-separator="."
            :value="value1"
            :title="title"
          >
            <template slot="prefix">
              <i class="el-icon-s-flag" style="color: red"></i>
            </template>
            <template slot="suffix">
              <i class="el-icon-s-flag" style="color: blue"></i>
            </template>
          </el-statistic>
        </div>
      </el-col>
      <el-col :span="6">
        <div>
          <el-statistic :value="like ? 521 : 520" title="Feedback">
            <template slot="suffix">
              <span @click="like = !like" class="like">
                <i
                  class="el-icon-star-on"
                  style="color: red"
                  v-show="!!like"
                ></i>
                <i class="el-icon-star-off" v-show="!like"></i>
              </span>
            </template>
          </el-statistic>
        </div>
      </el-col>
    </el-row>
    <div>
      <span><h1>昨年今年学生生源地情况对比（%千人）</h1></span>
      <span>
        <h1 style="float:left">昨年</h1>
        <div id="myChart2" style="width: 560px; height: 500px; float: left">
          静态获取
        </div>
      </span>
      <span>
        <h1 style="float:right">今年</h1>
        <div id="myChart1" style="width: 560px; height: 500px; float: right">
          动态获取
        </div>
      </span>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      like: true,
      value1: 4154,
      value2: 2222,
      title: '今年的增长',
      myChart1: '',
      opinionData3: [
        { value: 1, name: '北京', itemStyle: 'red' },
        { value: 3, name: '上海', itemStyle: '#1FC48D' },
        { value: 30, name: '四川', itemStyle: '#6DC8EC' },
        { value: 20, name: '其他', itemStyle: '#3F8FFF' }
      ],
      opinionData2: []
    }
  },
  mounted () {
    this.drawLine()
  },
  methods: {
    async drawLine () {
      await this.axios.get('/api/getAddress/lessonInfo').then((res) => {
        const array1 = res.data.list
        const m1 = new Map()
        const unique1 = (array, key) => {
          for (const item of array) {
            if (!m1.has(item[key])) {
              m1.set(item[key], item)
            }
          }
          return [...m1.values()]
        }
        const a1 = unique1(array1, 'name')
        this.opinionData2 = a1
      })
      this.myChart1 = this.$echarts.init(document.getElementById('myChart1'))
      this.myChart2 = this.$echarts.init(document.getElementById('myChart2'))
      this.myChart1.setOption({
        // 防止标签溢出
        grid: { containLabel: true },
        // 鼠标悬浮显示
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {d}%'
        },
        color: ['red', '#1FC48D', '#6DC8EC', '#3F8FFF'],
        // 对图形的解释
        legend: {
          orient: 'vertical',
          icon: 'circle',
          align: 'left',
          x: 'right',
          y: 'top',
          data: ['北京', '上海', '四川', '其他']
        },
        series: [
          {
            name: '学生生地',
            type: 'pie',
            radius: ['50%', '70%'],
            // 是否防止标签重叠
            avoidLabelOverlap: false,
            center: ['40%', '50%'],
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              },
              color: function (params) {
                // 自定义颜色
                const colorList = ['red', '#1FC48D', '#6DC8EC', '#3F8FFF']
                return colorList[params.dataIndex]
              }
            },
            data: this.opinionData2
          }
        ]
      })
      this.myChart2.setOption({
        // 防止标签溢出
        grid: { containLabel: true },
        // 鼠标悬浮显示
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {d}%'
        },
        // color: ['#1FC48D', '#F5A60A', '#6DC8EC', '#3F8FFF'],
        color: ['red', '#1FC48D', '#6DC8EC', '#3F8FFF'],
        // backgroundColor: '#ffffff',
        // 对图形的解释
        legend: {
          orient: 'vertical',
          icon: 'circle',
          align: 'left',
          x: 'left',
          y: 'top',
          data: ['北京', '上海', '四川', '其他']
        },
        series: [
          {
            name: '学生生地',
            type: 'pie',
            radius: ['50%', '70%'],
            // 是否防止标签重叠
            avoidLabelOverlap: false,
            center: ['40%', '50%'],
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              },
              color: function (params) {
                // 自定义颜色
                const colorList = ['red', '#1FC48D', '#6DC8EC', '#3F8FFF']
                return colorList[params.dataIndex]
              }
            },
            data: this.opinionData3
          }
        ]
      })
    }
  }
}
</script>

<style>
.like {
  cursor: pointer;
  font-size: 25px;
  display: inline-block;
}
</style>
